<!--created by wanghan-->
<template>
  <div>
    <p class="title">
      
      <el-button :disabled="constants.length===0" type="primary" size="mini" @click="count"></el-button>
    </p>
    <searchForm :fields="fields" @search="search"></searchForm>
    <contentTable
      v-loading="tableLoading" element-loading-background="rgba(0, 0, 0, 0.6)"
      ref="table"
      :titles="titles"
      :constants="constants"
      :totalPage="0"
      :noOperat="true">
      <template slot="tdOperate" slot-scope="{item}">
        <button type="button" class="btn btn-intab" @click="edit(item)"></button>
        <button type="button" class="btn btn-intab" @click="del(item)"></button>
      </template>
    </contentTable>
    <el-dialog :visible.sync="showEditForm" :title="editData._id?'':''" width="470px" :close-on-click-modal="false">
      <edit-form
        v-if="showEditForm"
        :fields="editFields"
        :editData="editData"
        @save="save"
        label-width="120px"
        @close="showEditForm=false">
      </edit-form>
    </el-dialog>
    <el-dialog :visible.sync="showCount" title="" width="470px" :close-on-click-modal="false">
      <table class="table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(item,index) in countResult">
          <td>{{item.name}}</td>
          <td>{{transNumShow(item.selfCount)}}</td>
          <td>{{transNumShow(item.snacksFund)}}</td>
          <td>{{transNumShow(item.rest)}}</td>
        </tr>
        <tr>
          <th></th>
          <td>{{transNumShow(summation.selfCount)}}</td>
          <td>{{transNumShow(summation.snacksFund)}}</td>
          <td>{{transNumShow(summation.rest)}}</td>
        </tr>
      </table>
      <div class="btn_footer">
        <el-input v-model="remark" placeholder="" style="margin-bottom: 10px;"></el-input>
        <el-button type="primary" size="mini" @click="apply"></el-button>
      </div>
      <!--<el-card v-for="(item,key,index) in countResult" class="box-card" >-->
      <!--{{key}}-->
      <!--{{item.selfCount}}-->
      <!--{{item.selfCount>35?35:0}}-->
      <!--</el-card>-->
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import contentTable from '../common/contentTable'
  import {addCount, addOvertime, delOvertime, editOvertime, getOvertimeList} from "../../api";
  import editForm from "../common/editForm";
  import searchForm from "../common/searchForm";
  export default {
    name: '',
    components: {
      contentTable,
      editForm,
      searchForm
    },
    data() {
      return {
        tableLoading:false,
        remark:'',
        showCount:false,
        showEditForm:false,
        countResult:{snacksFund:0,selfCount:0,rest:0},
        summation:{},
        editData:{},
        constants:[],
        titles:[
          {name:'',value:'name'},
          {name:'',value:'date',timeFormat:'yyyy-MM-dd'},
          {name:'',value:'payMoney'},
          {name:'',value:'payType',options:[{label:'',value:'self'},{label:'',value:'other'}]},
          {name:'',value:'payForMe'},
          {name:'',value:'status',options:[{label:'',value:'created'},{label:'',value:'counted'},{label:'',value:'applied'}]},
          {name:'',value:'remark'},

        ],
        fields:{
          name:{type: 'select', label: '', required:true,
            options:[],keyValue:{label:'name',value:'name'}},
          date:{type: 'daterange', label: '', required:true},
        },
        editFields:{
          name:{type: 'select', label: '', required:true,
            options:[],keyValue:{label:'name',value:'name'}},
          date:{type: 'date', label: '', required:true},
          overtimeType:{type: 'select',value:'dinner', label: '', required:true,
            options:[{label:'',value:'dinner'},{label:'',value:'lunch'}]},
          payType:{type: 'select',value:'self', label: '', required:true,
            options:[{label:'',value:'self'},{label:'',value:'other'}]},
          payForMe:{type: 'select', label: '',
            options:[],keyValue:{label:'name',value:'name'}},
          remark:{type: 'input', label: ''},
        },
        countDateBegin:'',
        countDateEnd:'',
      }
    },
    computed: {
      overtimeList(){
        let arr=this.constants.map((item)=>{
          return item._id;
        });
        return arr;
      }
    },
    watch:{
    },
    created() {
      this.init();
      this.fields.name.options=this.$store.state.userList || [];
      this.editFields.name.options=this.$store.state.userList || [];
      this.editFields.payForMe.options=this.$store.state.userList || [];
    },
    methods: {
      //
      apply(){
        this.$confirm('','').then(()=>{
          let params={
            countDateBegin:this.constants[0].date,
            overtimeList:this.overtimeList,
            countDateEnd:this.constants[this.constants.length-1].date,
            status:'counted',
            countResult:this.countResult,
            summation:this.summation,
            remark:this.remark
          };
          addCount(params).then(res => {
            this.showCount=false;
            this.constants.length=0;
            this.remark='';
            this.$message({type:'success',message:''})
          }).catch(err =>{});
        }).catch(()=>{})
      },
      //
      count(){
        let userList=this.$store.state.userList || [];
        let countResult={};
        this.countResult=[];
        userList.forEach(item=>{
          countResult[item.name]={
            selfCount:0,
            type:item.type
          };
        });
        this.constants.forEach(item=>{
          if(item.payType==='self'){
            countResult[item.name].selfCount+=35;
          }else {
            countResult[item.payForMe].selfCount+=item.payMoney;
            countResult[item.name].selfCount+=(35-item.payMoney);
          }
        });
        this.summation={snacksFund:0,selfCount:0,rest:0};
        for(let i in countResult){
          if(countResult[i].selfCount<0){
            countResult[i].snacksFund=0;
            countResult[i].rest=countResult[i].selfCount;
          }else if(countResult[i].selfCount===0){
            countResult[i].snacksFund=0;
            countResult[i].rest=0;
          }else if(countResult[i].selfCount>0 && countResult[i].selfCount<=35){
            countResult[i].snacksFund=countResult[i].selfCount;
            countResult[i].rest=0;
          }else if(countResult[i].selfCount>35){
            countResult[i].snacksFund=35;
            countResult[i].rest=countResult[i].selfCount-35;
          }
          this.countResult.push({
            name:i,
            selfCount:countResult[i].selfCount,
            snacksFund:countResult[i].snacksFund,
            rest:countResult[i].rest,
            type:countResult[i].type,
          });
          this.summation.snacksFund+=countResult[i].snacksFund;
          this.summation.selfCount+=countResult[i].selfCount;
          this.summation.rest+=countResult[i].rest;
        }
        this.showCount=true;
      },
      //
      init(){
        this.tableLoading=true;
        getOvertimeList({...this.condition,status:'created'}).then(res => {
          this.constants = res.data;
          this.constants.sort((a,b)=>{
            return new Date(a.date).getTime()-new Date(b.date).getTime()
          });
          this.tableLoading=false;
        }).catch(err=>{this.tableLoading=false;})
      },
      //
      search(val){
        this.condition={...val};
        this.condition.dateBegin=val.date?val.date[0]:null;
        this.condition.dateEnd=val.date?val.date[1]:null;
        this.init()
      },
      //
      edit(item){
        this.editData=item;
        this.showEditForm=true;
      },
      //
      del(item){
        this.$confirm('','').then(()=>{
          delOvertime({_id:item._id}).then(res => {
            this.init();
            this.$message({type:'success',message:''})
          }).catch(err =>{});
        }).catch(()=>{})
      },
      //
      save(val){
        let params={...val};
        let api=addOvertime;
        let Msg='';
        if(this.editData._id){
          api= editOvertime;
          Msg='';
        }
        api(params).then(res => {
          this.init();
          this.showEditForm=false;
          this.$message({type:'success',message:Msg})
        }).catch(err =>{});
      },
      transNumShow(num){
        if(num && typeof num==='number'){
          return num.toFixed(0)
        }else {
          return 0
        }
      }
    }
  }

</script>

<style scoped>
.btn_footer{
  margin-top: 26px;
  text-align: center;
}
</style>



