<!--created by wanghan-->
<template>
  <div>
    <p class="title">
      
      <el-button type="primary" size="mini" @click="edit"></el-button>
    </p>
    <searchForm :fields="fields" @search="search"></searchForm>
    <contentTable
      v-loading="tableLoading" element-loading-background="rgba(0, 0, 0, 0.6)"
      ref="table"
      :titles="titles"
      :constants="constants"
      :totalPage="totalPage"
      :pageParams="searchParam"
      @goPage="nextPage"
      :noOperat="false">
      <template slot="tdOperate" slot-scope="{item}">
        <button type="button" class="btn btn-intab" @click="edit(item)"></button>
        <button type="button" class="btn btn-intab" @click="del(item)"></button>
      </template>
    </contentTable>
    <el-dialog :visible.sync="showEditForm" :title="editData._id?'':''" width="470px" :close-on-click-modal="false">
      <edit-form
        v-if="showEditForm"
        :fields="editFields"
        :editData="editData"
        @save="save"
        label-width="120px"
        @close="showEditForm=false">
      </edit-form>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import contentTable from '../common/contentTable'
  import {addOvertime, delOvertime, editOvertime, getOvertimeList} from "../../api";
  import editForm from "../common/editForm";
  import searchForm from "../common/searchForm";
  import {options} from './datePickerOptions.js'
  export default {
    name: '',
    components: {
      contentTable,
      editForm,
      searchForm
    },
    data() {
      return {
        tableLoading:false,
        showEditForm:false,
        editData:{},
        condition:{},
        searchParam:{
          page:1,
          pageSize:15
        },
        totalPage:0,
        constants:[],
        titles:[
          {name:'',value:'name'},
          {name:'',value:'date',timeFormat:'yyyy-MM-dd'},
          // {name:'',value:'overtimeType',options:[{label:'',value:'dinner'},{label:'',value:'lunch'}]},
          {name:'',value:'payMoney'},
          {name:'',value:'payType',options:[{label:'',value:'self'},{label:'',value:'other'}]},
          {name:'',value:'payForMe'},
          {name:'',value:'status',options:[{label:'',value:'created'},{label:'',value:'counted'},{label:'',value:'applied'}]},
          // {name:'',value:'payForWho'},
          {name:'',value:'remark'},
          {name:'',value:'createTime',timeFormat:'yyyy-MM-dd hh:mm:ss'},
          {name:'',value:'updateTime',timeFormat:'yyyy-MM-dd hh:mm:ss'},

        ],
        fields:{
          name:{type: 'select', label: '', required:true,value:'',
            options:[],keyValue:{label:'name',value:'name'}},
          status:{type: 'select', label: '', required:true,
            options:[{label:'',value:'created'},{label:'',value:'counted'},{label:'',value:'applied'}]},
          payType:{type: 'select', label: '', required:true,
            options:[{label:'',value:'self'},{label:'',value:'other'}]},
          date:{type: 'date', label: ''},
          dateRange:{type: 'daterange',value:[], label: '', required:true,pickerOptions:options},
        },
        editFields:{
          name:{type: 'select', value:'',label: '', required:true,editDisabled:true,
            options:[],keyValue:{label:'name',value:'name'}},
          date:{type: 'dates', label: '', required:true,editDisabled:true},
          // overtimeType:{type: 'select',value:'dinner', label: '', required:true,
          //   options:[{label:'',value:'dinner'},{label:'',value:'lunch'}]},
          payType:{type: 'select',value:'self', label: '', required:true,
            options:[{label:'',value:'self'},{label:'',value:'other'}]},
          payForMe:{type: 'select', label: '',
            options:[],keyValue:{label:'name',value:'name'}},
          payMoney:{type: 'number', label: ''},
          remark:{type: 'input', label: ''},
        },
      }
    },
    computed: {
    },
    watch:{
      '$store.state.userList':{
        handler:function (val) {
          let userList=val || [];
          this.fields.name.options=userList;
          this.editFields.name.options=userList;
          this.editFields.payForMe.options=userList;
        },
        immediate:true
      },
      '$store.state.curUser':{
        handler:function (val) {
          this.fields.name.value=val;
          this.editFields.name.value=val;
          this.condition.name=val;
          this.$nextTick(()=>{
            this.getThreeMonth();
            this.init();
          });
        },
        immediate:true
      }
    },
    created() {
    },
    methods: {
      //
      getThreeMonth(){
        let now = new Date();
        let start=now.getTime()-3*30*24*60*60*1000;
        let dateRange=[new Date(start).format('yyyy-MM-dd'),new Date(now).format('yyyy-MM-dd')];
        this.fields.dateRange.value=dateRange;
        this.condition.dateBegin=dateRange[0];
        this.condition.dateEnd=dateRange[1];
      },
      init(){
        this.tableLoading=true;
        getOvertimeList({...this.searchParam,...this.condition}).then(res => {
          this.constants = res.data;
          this.totalPage=Math.ceil(res.total/this.searchParam.pageSize);
          this.tableLoading=false;
        }).catch(err=>{this.tableLoading=false;})
      },
      //
      search(val){
        this.condition={...val};
        this.condition.dateBegin=val.dateRange?val.dateRange[0]:null;
        this.condition.dateEnd=val.dateRange?val.dateRange[1]:null;
        this.init()
      },
      //
      nextPage(jumpPage){
        this.searchParam.page=jumpPage.page;
        this.init();
      },
      //
      edit(item){
        this.editData=item;
        this.showEditForm=true;
      },
      //
      del(item){
        this.$confirm('','').then(()=>{
          delOvertime({_id:item._id}).then(res => {
            this.init();
            this.$message({type:'success',message:''})
          }).catch(err =>{});
        }).catch(()=>{})
      },
      //
      save(val){
        let params={...val};
        if(params.payType==='self'){
          params.payMoney=35;
          params.payForMe=null;
        }else if(!params.payForMe){
          this.$message({type:'warning',message:''});
          return
        }else if(!params.payMoney){
          this.$message({type:'warning',message:''});
          return
        }
        let api=addOvertime;
        let Msg='';
        if(this.editData._id){
          api= editOvertime;
          Msg='';
        }else {
          params.date=JSON.stringify(params.date);
        }
        // return
        api(params).then(res => {
          this.init();
          this.showEditForm=false;
          this.$message({type:'success',message:Msg})
        }).catch(err =>{
          let errDates='';
          err.data.forEach(item=>{
            errDates+=new Date(item).format('yyyy-MM-dd');
            errDates+=','
          });
          this.$message({type:'warning',message:`${errDates}`})
        });
      },
    }
  }

</script>

<style scoped>

</style>



