<!--created by wanghan-->
<template>
  <div>
    <p class="title">
      
      <el-button type="primary" size="mini" @click="edit"></el-button>
    </p>
    <searchForm :fields="fields" @search="search"></searchForm>
    <contentTable
      v-loading="tableLoading" element-loading-background="rgba(0, 0, 0, 0.6)"
      ref="table"
      :titles="titles"
      :constants="constants"
      :totalPage="totalPage"
      :pageParams="searchParam"
      @goPage="nextPage"
      :noOperat="false">
      <template slot="tdOperate" slot-scope="{item}">
        <button type="button" class="btn btn-intab" @click="edit(item)"></button>
        <button v-show="true" type="button" class="btn btn-intab" @click="del(item)"></button>
      </template>
    </contentTable>
    <el-dialog :visible.sync="showEditForm" :title="editData._id?'':''" width="470px" :close-on-click-modal="false">
      <edit-form
        v-if="showEditForm"
        :fields="editFields"
        :editData="editData"
        @save="save"
        label-width="120px"
        @close="showEditForm=false">
      </edit-form>
    </el-dialog>

  </div>
</template>

<script type="text/javascript">
  import contentTable from '../common/contentTable'
  import {addPay, delPay, editPay, getPayList} from "../../api";
  import editForm from "../common/editForm";
  import searchForm from "../common/searchForm";
  import {options} from './datePickerOptions.js'
  export default {
    name: '',
    components: {
      contentTable,
      editForm,
      searchForm
    },
    data() {
      return {
        tableLoading:false,
        showEditForm:false,
        editData:{},
        condition:{},
        searchParam:{
          page:1,
          pageSize:15
        },
        totalPage:0,
        constants:[],
        titles:[
          {name:'',value:'payer'},
          {name:'',value:'eater'},
          {name:'',value:'payMoney'},
          {name:'',value:'payDay',timeFormat:'yyyy-MM-dd'},
          {name:'',value:'remark'},
          // {name:'',value:'validFrom',timeFormat:'yyyy-MM-dd hh:mm:ss'},
          // {name:'',value:'validTo',timeFormat:'yyyy-MM-dd hh:mm:ss'},

        ],
        fields:{
          payer:{type: 'select',value:'', label: '',options:[],keyValue:{label:'name',value:'name'}},
          payDay:{type: 'date', label: ''},
          dateRange:{type: 'daterange',value:[], label: '', required:true,pickerOptions:options},
        },
        editFields:{
          payer:{type: 'select',value:'', label: '', required:true,
            options:[],keyValue:{label:'name',value:'name'}},
          eater:{type: 'select', label: '',value:[],multiple:true,filterable:false, required:true,
            options:[],keyValue:{label:'name',value:'name'}},
          payMoney:{type: 'number', label: '', required:true},
          payDay:{type: 'date', label: '', required:true,editDisabled: true},
          remark:{type: 'input', label: ''},
        },
      }
    },
    computed: {
    },
    watch:{
      '$store.state.userList':{
        handler:function (val) {
          let userList=val || [];
          this.fields.payer.options=userList;
          this.editFields.payer.options=userList;
          this.editFields.eater.options=userList;
        },
        immediate:true
      },
      '$store.state.curUser':{
        handler:function (val) {
          this.fields.payer.value=val;
          this.editFields.payer.value=val;
          this.condition.payer=val;
          this.$nextTick(()=>{
            this.getThreeMonth();
            this.init();
          });
        },
        immediate:true
      }
    },
    created() {
    },
    methods: {
      //
      getThreeMonth(){
        let now = new Date();
        let start=now.getTime()-3*30*24*60*60*1000;
        let dateRange=[new Date(start).format('yyyy-MM-dd'),new Date(now).format('yyyy-MM-dd')];
        this.fields.dateRange.value=dateRange;
        this.condition.dateBegin=dateRange[0];
        this.condition.dateEnd=dateRange[1];
      },
      init(){
        this.tableLoading=true;
        getPayList({...this.searchParam,...this.condition}).then(res => {
          this.constants = res.data;
          this.totalPage=Math.ceil(res.total/this.searchParam.pageSize);
          this.tableLoading=false;
        }).catch(err=>{this.tableLoading=false;})
      },
      //
      search(val){
        this.condition={...val};
        this.condition.dateBegin=val.dateRange?val.dateRange[0]:null;
        this.condition.dateEnd=val.dateRange?val.dateRange[1]:null;
        this.init()
      },
      //
      nextPage(jumpPage){
        this.searchParam.page=jumpPage.page;
        this.init();
      },
      //
      edit(item){
        this.editData=item;
        this.showEditForm=true;
      },
      //
      del(item){
        this.$confirm('','').then(()=>{
          delPay({_id:item._id}).then(res => {
            this.init();
            this.$message({type:'success',message:''})
          }).catch(err =>{});
        }).catch(()=>{})
      },
      //
      save(val){
        let params={...val};
        if(params.eater.indexOf(params.payer)===-1){
          params.eater.push(params.payer);
        }
        let api=addPay;
        let Msg='';
        if(this.editData._id){
          api= editPay;
          Msg='';
        }
        api(params).then(res => {
          this.init();
          this.showEditForm=false;
          this.$message({type:'success',message:Msg})
        }).catch(err =>{});
      },
    }
  }

</script>

<style scoped>

</style>



